import { FETCH_EVENTS_START, FETCH_EVENTS_SUCCESS, FETCH_EVENTS_FAILURE } from './action'

export function events (state = [], action) {
  switch (action.type) {
    case FETCH_EVENTS_START:
      return state
    case FETCH_EVENTS_SUCCESS:
      return action.data
    case FETCH_EVENTS_FAILURE:
      return state
    default:
      return state
  }
}

export function eventCount (state = 0, action) {
  switch (action.type) {
    case FETCH_EVENTS_START:
      return state
    case FETCH_EVENTS_SUCCESS:
      return action.count
    case FETCH_EVENTS_FAILURE:
      return state
    default:
      return state
  }
}

export function eventHeading (state = '', action) {
  return 'Upcoming Events'
}

export function eventSubheading (state = '', action) {
  switch (action.type) {
    case FETCH_EVENTS_SUCCESS:
      return `Over ${action.count} events has been created in the past. Keep up to date with the latest events here!`
    default:
      return state
  }
}

export function eventCallToActionUrl (state = '', action) {
  return 'https://events.engineers.my/'
}



