import React from 'react'
import PropTypes from 'prop-types'

import './index.css'

import Br from 'components/atoms/Break'
import ProfileCard from 'components/molecules/ProfileCard'

const Component = ({ members, heading, subheading }) => (
  <div className='member-holder'>
    <Br />
    <h2 className='member-heading'>
      {heading}
    </h2>
    <p className='member-subheading'>
      {subheading}
    </p>
    <div className='profile-holder'>
      {
        members.map(({ name, biography, profileImg }) => (
          <ProfileCard key={name} name={name} biography={biography} profileImg={profileImg} />
        ))
      }
    </div>
    {/* <div className='community-holder-footer'>How do I be a community manager?</div> */}
    <Br />
  </div>
)

Component.propTypes = {
  members: PropTypes.arrayOf(PropTypes.shape({
    name: PropTypes.string.isRequired,
    biography: PropTypes.string.isRequired,
    profileImg: PropTypes.string.isRequired
  })),
  heading: PropTypes.string.isRequired,
  subheading: PropTypes.string.isRequired
}

export default Component



