import React from 'react'
import PropTypes from 'prop-types'

import './index.css'

import Br from 'components/atoms/Break'
import Button from 'components/atoms/Button'
import Code from 'components/atoms/Code'

const Jumbotron = ({ heading, subheading, slackUrl }) => (

  <div className='jumbotron'>
    <Code />
    <h1 className='jumbotron-heading'>{heading}</h1>
    <h4 className='jumbotron-subheading'>{subheading}</h4>
    <Br />
    <div>Find us on Slack</div>
    <Br />
    <Button href={slackUrl}><i className='fa fa-slack' /> Slack</Button>
  </div>
)

Jumbotron.propTypes = {
  heading: PropTypes.string.isRequired,
  subheading: PropTypes.string.isRequired,
  slackUrl: PropTypes.string.isRequired
}

export default Jumbotron



