import React from 'react'
import { connect } from 'react-redux'

// Styles
import './index.css'

// Molecules
import Jumbotron from 'components/molecules/Jumbotron'

// Organisms
import Base from 'components/organisms/Base'
import Member from 'components/organisms/Member'
import Event from 'components/organisms/Event'
import Br from 'components/atoms/Break'
import { fetchEventsStart } from 'redux/reducers/event/action-creator'

class Home extends React.Component {
  componentWillMount () {
    // Fetch the events before mounting the components
    this.props.fetchEvents()
  }
  render () {
    const {
      // Events
      events,
      eventCount,
      eventHeading,
      eventSubheading,
      eventCallToActionUrl,
      // Members
      members,
      memberHeading,
      memberSubheading
    } = this.props

    return (
      <Base {...this.props}>

        <Jumbotron
          heading='engineers.my'
          subheading='An open community for developers/designers to share, collaborate and improve the tech community in Malaysia.'
          slackUrl='https://engineersmy.herokuapp.com/'
        />
        <Br />

        <Event
          heading={eventHeading}
          subheading={eventSubheading}
          events={events}
          count={eventCount}
          callToActionUrl={eventCallToActionUrl}
        />

        <Br />

        {/* <div className='meetup-holder'>
          <Br />

          <div className='meetup-holder-title'><i className='fa fa-map' /> Meetups Areas</div>
          <Br />

          <div>Find out where we do our meetup...</div>

          <Br />
        </div>

        <Br /> */}

        {/* <div className='volunteer-holder'>
          <Br />

          <div className='volunteer-holder-title'><i className='fa fa-camera' /> Volunteer for Recording/Meetups</div>
          <Br />

          <div>Wanna help out at local meetups and learn new stuff? Volunteer here!</div>
          <Br />
        </div> */}

        {/* <Br /> */}

        <Member
          members={members}
          heading={memberHeading}
          subheading={memberSubheading}
        />

        <br />
      </Base>
    )
  }
}

const mapStateToProps = (state) => ({
  members: state.members,
  memberHeading: state.memberHeading,
  memberSubheading: state.memberSubheading,
  events: state.events.filter(event => new Date(event.start_date).getTime() >= Date.now()),
  eventCount: state.eventCount,
  eventHeading: state.eventHeading,
  eventSubheading: state.eventSubheading,
  eventCallToActionUrl: state.eventCallToActionUrl
})

const mapDispatchToProps = (dispatch) => ({
  fetchEvents () {
    dispatch(fetchEventsStart())
  }
})

export default connect(mapStateToProps, mapDispatchToProps)(Home)



