import React from 'react'

import './index.css'

import Br from 'components/atoms/Break'
import Button from 'components/atoms/Button'

const Component = ({ heading, subheading, events, count, callToActionUrl }) => (
  <div className='event-holder'>
    <Br />
    <div className='event-holder-title'>{heading}</div>
    <Br />

    <div className='event-list-holder'>
      {
        events.map(event => (
          <div className='event' key={event.id}>
            <div className='event-dot' />
            <div className='event-info'><b className='event-time'>{parseDate(event.start_date)}</b> - {event.name}</div>
          </div>
        ))
      }
    </div>

    <Br />
    <div className='event-holder-footer'>
      <small>{subheading}</small>
    </div>

    <Br />
    <div className='event-call-to-action-holder'>
      <Button href={callToActionUrl}>Contribute</Button>
    </div>
    <Br />
  </div>
)

const MONTHS = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']
function parseDate (timestamp) {
  const dateObj = new Date(timestamp)
  const month = dateObj.getMonth()
  const date = dateObj.getDate()
  const monthStr = MONTHS[month]
  return `${monthStr} ${date}`
}

export default Component



