import { FETCH_EVENTS_START, FETCH_EVENTS_SUCCESS, FETCH_EVENTS_FAILURE } from './action'

export function fetchEventsStart () {
  return {
    type: FETCH_EVENTS_START
  }
}

export function fetchEventsSuccess ({ data, count }) {
  return {
    type: FETCH_EVENTS_SUCCESS,
    data,
    count
  }
}

export function fetchEventsFailure (message) {
  return {
    type: FETCH_EVENTS_FAILURE,
    message
  }
}



