import React from 'react'
import { Link } from 'react-router-dom'

import './index.css'

const Header = () => (
  <div className='header'>
    <Link className='brand' to='/'>
      <div className='brand-icon' />&nbsp;&nbsp;
    </Link>
  </div>
)

export default Header



