import { call, takeLatest, put } from 'redux-saga/effects'
import { fetchEventsSuccess, fetchEventsFailure } from '../reducers/event/action-creator'

import { getLatestEvents } from 'service/event'

function * fetchEvents (action) {
  try {
    const events = yield call(getLatestEvents, null)
    yield put(fetchEventsSuccess(events))
  } catch (e) {
    yield put(fetchEventsFailure(e.message))
  }
}

function * sagas () {
  yield takeLatest('FETCH_EVENTS_START', fetchEvents)
}

export default sagas



