// Contains the root state of the application that is shared globally

// NOTE:
// - Component specific data is written in this format - Pattern <component_name>:<role>
// - Instead of placing the default values as initial state while creating the store, placing
// it here gives better indication on what data the component expects

// footer returns the footer text of the website
export function footer (state = '', action) {
  return `Copyright  ${new Date().getFullYear()} engineers.my`
}

// members returns the list of members of engineers.my
export function members (state = [], action) {
  return [
    {
      name: 'Chow Chee Leong',
      biography: 'Head of Engineering, Tuxuri',
      profileImg: 'assets/cheeleong.jpg'
    },
    {
      name: 'Manjunath Reddy',
      biography: 'Fullstack Developer, iFlix',
      profileImg: 'assets/manju.jpg'
    },
    {
      name: 'Michael Leow',
      biography: 'Backend Lead, Jirnexu',
      profileImg: 'assets/michaelleow.jpg'
    },
    {
      name: 'Ivan Tham Jun Hoe',
      biography: 'Student/Freelancer',
      profileImg: 'assets/ivanthamjunhoe.jpg'
    },
    {
      name: 'Alex Tan Hong Pin',
      biography: 'Developer Advocate, SeekAsia',
      profileImg: 'assets/alextanhongpin.jpg'
    }
  ]
}

// Return the heading of the member component
export function memberHeading (state = '', action) {
  return 'Community Members'
}

// Return the subheading of the member component
export function memberSubheading (state = '', action) {
  return 'Meet people from different companies and learn about how stuff is running in production.'
}



